import React from 'react'
import { PanelHeader, Panel, Card, Input, Checkbox, Button} from '@vkontakte/vkui'
import {Icon20ArticleOutline} from '@vkontakte/icons'

import './Home.css'

const Home = ({id, fetchedUser}) => {

    return (
    <Panel id={id}>
        <PanelHeader>Сокращение cсылок</PanelHeader>
        <Card mode="shadow" style={{margin: 5, padding: 5, fontSize: 14, textAlign: 'center'}}>
            <div >
На этой странице вы можете сделать из длинной и сложной ссылки простую.
Такие ссылки удобнее использовать в ваших записях и сообщениях.
            </div>
        </Card >
        <Input style={{margin: 5}} type="text" placeholder="Ссылка, которую вы хотите сократить" />
        <Checkbox>Статистика ссылки видна только вам</Checkbox>
        <Button size="m" style={{margin: 5}}>Сократить</Button>
    </Panel>
    )
}

export default Home